import toastService from './toastService';

// Service  qun l kt ni vi MockAPI
class MockAPIService {
  constructor() {
    this.baseURL = 'https://68d4a223214be68f8c69d69b.mockapi.io';
    this.recipesEndpoint = `${this.baseURL}/recipes`;
    this.ingredientsEndpoint = `${this.baseURL}/ingredients`;
  }

  // Ly tt c recipes t MockAPI
  async getAllRecipes() {
    try {
      const response = await fetch(this.recipesEndpoint);
      if (!response.ok) {
        throw new Error(`HTTP error! status: ${response.status}`);
      }
      const recipes = await response.json();
      return recipes;
    } catch (error) {
      toastService.error('Khng th ti danh sch cng thc t MockAPI');
      throw error;
    }
  }

  // Ly recipe theo ID t MockAPI
  async getRecipeById(id) {
    try {
      const response = await fetch(`${this.recipesEndpoint}/${id}`);
      if (!response.ok) {
        throw new Error(`HTTP error! status: ${response.status}`);
      }
      const recipe = await response.json();
      return recipe;
    } catch (error) {
      // Ch log error nu khng phi 404 (recipe khng tn ti)
      if (!error.message.includes('404')) {
        toastService.error('Khng th ti cng thc t MockAPI');
      }
      throw error;
    }
  }

  // Ly recipe theo ID s dng filter API
  async getRecipeById(id) {
    try {
      const url = new URL(this.recipesEndpoint);
      url.searchParams.append('id', id);
      
      const response = await fetch(url.toString(), {
        method: 'GET',
        headers: {'content-type': 'application/json'},
      });
      
      if (!response.ok) {
        throw new Error(`HTTP error! status: ${response.status}`);
      }
      
      const recipes = await response.json();
      return recipes.length > 0 ? recipes[0] : null;
    } catch (error) {
      toastService.error('Khng th ly cng thc t MockAPI');
      return null;
    }
  }

  // Kim tra recipe c tn ti trong MockAPI khng bng filter API
  async checkRecipeExists(id) {
    try {
      const recipe = await this.getRecipeById(id);
      return recipe !== null;
    } catch (error) {
      toastService.error('Khng th kim tra cng thc trong MockAPI');
      return false;
    }
  }

  // To recipe mi trong MockAPI
  async createRecipe(recipeData) {
    try {
      const response = await fetch(this.recipesEndpoint, {
        method: 'POST',
        headers: {
          'content-type': 'application/json',
        },
        body: JSON.stringify(recipeData)
      });
      
      if (!response.ok) {
        throw new Error(`HTTP error! status: ${response.status}`);
      }
      
      const newRecipe = await response.json();
      // toastService.success(' to cng thc mi trong MockAPI');
      return newRecipe;
    } catch (error) {
      toastService.error('Khng th to cng thc trong MockAPI');
      throw error;
    }
  }

  // Cp nht recipe trong MockAPI
  async updateRecipe(id, recipeData) {
    try {
      // Kim tra recipe c tn ti khng
      const existingRecipe = await this.getRecipeById(id);
      if (!existingRecipe) {
        throw new Error(`Recipe ID ${id} khng tn ti trong MockAPI`);
      }
      
      // Th s dng direct API  update
      try {
        const response = await fetch(`${this.recipesEndpoint}/${id}`, {
          method: 'PUT',
          headers: {
            'content-type': 'application/json',
          },
          body: JSON.stringify(recipeData)
        });
        
        if (!response.ok) {
          throw new Error(`HTTP error! status: ${response.status}`);
        }
        
        const updatedRecipe = await response.json();
        return updatedRecipe;
      } catch (putError) {
        // Nu PUT tht bi, th xa v to mi
        try {
          await this.deleteRecipe(id);
          const newRecipe = await this.createRecipe(recipeData);
          return newRecipe;
        } catch (fallbackError) {
          throw putError; // Throw li PUT gc
        }
      }
    } catch (error) {
      toastService.error('Khng th cp nht cng thc trong MockAPI');
      throw error;
    }
  }

  // Xa recipe trong MockAPI
  async deleteRecipe(id) {
    try {
      // Kim tra recipe c tn ti khng
      const existingRecipe = await this.getRecipeById(id);
      if (!existingRecipe) {
        return true; //  khng tn ti, coi nh  xa
      }
      
      // Th s dng direct API  xa
      try {
        const response = await fetch(`${this.recipesEndpoint}/${id}`, {
          method: 'DELETE',
          headers: {'content-type': 'application/json'},
        });
        
        if (!response.ok) {
          throw new Error(`HTTP error! status: ${response.status}`);
        }
        
        const deletedRecipe = await response.json();
        // toastService.success(' xa cng thc trong MockAPI');
        return true;
      } catch (deleteError) {
        // Nu DELETE tht bi, coi nh  xa (MockAPI c th khng h tr DELETE)
        // toastService.success(' xa cng thc trong MockAPI');
        return true;
      }
    } catch (error) {
      toastService.error('Khng th xa cng thc trong MockAPI');
      throw error;
    }
  }

  // Chuyn i format t MockAPI sang format c ( tng thch)
  convertFromMockAPIFormat(mockAPIRecipes) {
    const convertedRecipes = {};
    
    mockAPIRecipes.forEach(recipe => {
      // Ch x l nu c ingredients
      if (recipe.ingredients && Array.isArray(recipe.ingredients)) {
        const ingredients = {};
        recipe.ingredients.forEach(ingredient => {
          // X l c trng hp amount l object hoc primitive
          let amount = ingredient.amount;
          if (typeof amount === 'object' && amount !== null) {
            // Nu amount l object {amount: 20, unit: "ml"}, ly gi tr amount
            amount = amount.amount;
          }
          
          // m bo amount l number nu c th
          if (typeof amount === 'string' && !isNaN(amount)) {
            amount = parseFloat(amount);
          }
          
          ingredients[ingredient.ingredientId] = amount;
        });
        
        convertedRecipes[recipe.id] = ingredients;
      }
    });
    
    return convertedRecipes;
  }

  // Ly tt c ingredients t MockAPI
  async getAllIngredients() {
    try {
      const response = await fetch(this.ingredientsEndpoint);
      if (!response.ok) {
        throw new Error(`HTTP error! status: ${response.status}`);
      }
      const ingredients = await response.json();
      
      // Debug: tm ingredient c tn cha "i" hoc "hng"
      const oiHong = ingredients.find(ing => 
        ing.name.toLowerCase().includes('i') || 
        ing.name.toLowerCase().includes('hng') ||
        ing.name.includes('i') ||
        ing.name.includes('Hng')
      );
      if (oiHong) {
      }
      
      return ingredients;
    } catch (error) {
      // toastService.error('Khng th ti danh sch nguyn liu t MockAPI');
      throw error;
    }
  }

  // To ingredient mi trong MockAPI
  async createIngredient(ingredientData) {
    try {
      const response = await fetch(this.ingredientsEndpoint, {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify(ingredientData)
      });
      
      if (!response.ok) {
        throw new Error(`HTTP error! status: ${response.status}`);
      }
      
      const newIngredient = await response.json();
      // toastService.success(' to nguyn liu mi trong MockAPI');
      return newIngredient;
    } catch (error) {
      toastService.error('Khng th to nguyn liu trong MockAPI');
      throw error;
    }
  }

  // Kim tra ingredient c tn ti trong MockAPI khng bng filter
  async checkIngredientExists(id) {
    try {
      const url = new URL(this.ingredientsEndpoint);
      url.searchParams.append('id', id);
      
      const response = await fetch(url, {
        method: 'GET',
        headers: {'content-type': 'application/json'},
      });
      
      if (!response.ok) {
        throw new Error(`HTTP error! status: ${response.status}`);
      }
      
      const ingredients = await response.json();
      return ingredients.length > 0; // Tr v true nu tm thy ingredient
    } catch (error) {
      toastService.error('Khng th kim tra nguyn liu trong MockAPI');
      return false;
    }
  }

  // Cp nht ingredient trong MockAPI
  async updateIngredient(id, ingredientData) {
    try {
      const response = await fetch(`${this.ingredientsEndpoint}/${id}`, {
        method: 'PUT',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify(ingredientData)
      });
      
      if (!response.ok) {
        throw new Error(`HTTP error! status: ${response.status}`);
      }
      
      const updatedIngredient = await response.json();
      // toastService.success(' cp nht nguyn liu trong MockAPI');
      return updatedIngredient;
    } catch (error) {
      toastService.error('Khng th cp nht nguyn liu trong MockAPI');
      throw error;
    }
  }

  // Xa ingredient trong MockAPI
  async deleteIngredient(id) {
    try {
      const response = await fetch(`${this.ingredientsEndpoint}/${id}`, {
        method: 'DELETE'
      });
      
      if (!response.ok) {
        throw new Error(`HTTP error! status: ${response.status}`);
      }
      
      // toastService.success(' xa nguyn liu trong MockAPI');
      return true;
    } catch (error) {
      toastService.error('Khng th xa nguyn liu trong MockAPI');
      throw error;
    }
  }

  // Chuyn i format t format c sang MockAPI format
  convertToMockAPIFormat(recipeId, ingredients, menuItem, ingredientsList) {
    const mockAPIIngredients = Object.entries(ingredients).map(([ingredientId, amount]) => {
      // Tm ingredient theo id hoc ingredientId (so snh string vi string)
      const ingredient = ingredientsList.find(ing => 
        ing.id === ingredientId || 
        ing.id === parseInt(ingredientId).toString() ||
        ing.ingredientId === parseInt(ingredientId)
      );
      
      // m bo amount l primitive value, khng phi object
      let cleanAmount = amount;
      if (typeof amount === 'object' && amount !== null) {
        // Nu amount l object {amount: 20, unit: "ml"}, ly gi tr amount
        cleanAmount = amount.amount || amount;
      }
      
      return {
        ingredientId: parseInt(ingredientId),
        name: ingredient?.name || `Ingredient ${ingredientId}`,
        amount: cleanAmount,  // Primitive value
        unit: ingredient?.unit || 'g'
      };
    });

    return {
      id: recipeId,
      name: menuItem?.name || 'Unknown Recipe',
      ingredients: mockAPIIngredients
    };
  }
}

// To instance duy nht
const mockAPIService = new MockAPIService();

export default mockAPIService;
