import React from 'react';
import { Link } from 'react-router-dom';

const HomePage = () => {
  return (
    <div className="container" style={{ paddingTop: '2rem', paddingBottom: '2rem' }}>
      {/* Hero Section */}
      <div className="card text-center fade-in" style={{ 
        background: 'linear-gradient(135deg, #667eea 0%, #764ba2 100%)',
        color: 'white',
        border: 'none',
        marginBottom: '3rem'
      }}>
        <div style={{ padding: '3rem 2rem' }}>
          <h1 className="text-xl" style={{ 
            marginBottom: '1rem',
            fontSize: '3rem',
            fontWeight: 'bold'
          }}>
             Ingredients Tool
          </h1>
          <p className="text-lg" style={{ 
            marginBottom: '2rem',
            opacity: 0.9,
            maxWidth: '600px',
            margin: '0 auto 2rem auto'
          }}>
            Cng c qun l nguyn liu v cng thc chuyn nghip cho qun cafe
          </p>
          <div className="flex flex-center gap-2" style={{ flexWrap: 'wrap' }}>
            <Link to="/inventory" className="btn btn-primary btn-lg">
               Kim K Nguyn Liu
            </Link>
            <Link to="/recipes" className="btn btn-success btn-lg">
               Qun L Cng Thc
            </Link>
            <Link to="/ingredients" className="btn btn-warning btn-lg">
               Qun L Nguyn Liu
            </Link>
          </div>
        </div>
      </div>

      {/* Features Section */}
      <div className="grid grid-3" style={{ marginBottom: '3rem' }}>
        <div className="card text-center fade-in">
          <div style={{ fontSize: '3rem', marginBottom: '1rem' }}></div>
          <h3 className="text-lg font-bold mb-2">Kim K Thng Minh</h3>
          <p className="text-muted">
            Theo di s lng bn hng v tnh ton nguyn liu  s dng mt cch chnh xc
          </p>
        </div>
        
        <div className="card text-center fade-in">
          <div style={{ fontSize: '3rem', marginBottom: '1rem' }}></div>
          <h3 className="text-lg font-bold mb-2">Qun L Cng Thc</h3>
          <p className="text-muted">
            To v chnh sa cng thc cho tng mn vi giao din trc quan, d s dng
          </p>
        </div>
        
        <div className="card text-center fade-in">
          <div style={{ fontSize: '3rem', marginBottom: '1rem' }}></div>
          <h3 className="text-lg font-bold mb-2">Tnh Ton Doanh Thu</h3>
          <p className="text-muted">
            T ng tnh ton tng doanh thu v chi ph nguyn liu theo tng ngy
          </p>
        </div>
      </div>

      {/* Stats Section */}
      <div className="card fade-in" style={{ 
        background: 'linear-gradient(135deg, #f093fb 0%, #f5576c 100%)',
        color: 'white',
        border: 'none'
      }}>
        <div className="text-center mb-3">
          <h2 className="text-xl font-bold">Thng K H Thng</h2>
        </div>
        <div className="grid grid-4 text-center">
          <div>
            <div className="text-lg font-bold">25</div>
            <div className="text-sm" style={{ opacity: 0.9 }}>Mn  Ung</div>
          </div>
          <div>
            <div className="text-lg font-bold">26</div>
            <div className="text-sm" style={{ opacity: 0.9 }}>Nguyn Liu</div>
          </div>
          <div>
            <div className="text-lg font-bold">25</div>
            <div className="text-sm" style={{ opacity: 0.9 }}>Cng Thc</div>
          </div>
          <div>
            <div className="text-lg font-bold">100%</div>
            <div className="text-sm" style={{ opacity: 0.9 }}>T ng Ha</div>
          </div>
        </div>
      </div>

      {/* Quick Actions */}
      <div className="card fade-in">
        <h3 className="text-lg font-bold mb-3 text-center">Thao Tc Nhanh</h3>
        <div className="grid grid-3 gap-2">
          <Link to="/inventory" className="btn btn-primary">
             Xem Kim K Hm Nay
          </Link>
          <Link to="/recipes" className="btn btn-success">
             Chnh Sa Cng Thc
          </Link>
          <Link to="/ingredients" className="btn btn-warning">
             Qun L Nguyn Liu
          </Link>
        </div>
      </div>
    </div>
  );
};

export default HomePage;
