import * as XLSX from 'xlsx';

class ExcelService {
  constructor() {
    this.templateUrl = 'https://docs.google.com/spreadsheets/d/1zjRfnTCH_gxBQpQ7-jN4O3KmY9ew_oHjMb629narNok/edit?gid=0#gid=0';
  }

  // Danh sch nguyn liu theo th t c nh
  getFixedIngredientOrder() {
    return [
      'C PH',
      'SA C',
      'MT VIT QUT',
      'CT DA (400ML/ LON)',
      'TR COZY (100 GI)',
      'TR GNG',
      'MATCHA',
      'MT ONG (1000ML/ CHAI)',
      'THUC 3 S VIT',
      'THUC MO MI',
      'THUC MO ',
      'HNG DNG',
      'BNH GU',
      'BNH TAI HEO',
      'BNH QUE',
      'SA TI (1L/ HP)',
      'SA OATSIDE (1L/ HP)',
      'RICH (454G/ HP)',
      'SA CHUA (100G/ H)',
      'PEPSI',
      'STING',
      'REDBULL',
      'I',
      'CAM SNH',
      'THM',
      'DA',
      'TO',
      'B',
      'NP UNG TRC TIP',
      'LY PET 360ML (50 LY/ CY)'
    ];
  }

  // To d liu kim k t inventory data theo th t c nh
  generateInventoryData(inventoryData, ingredients) {
    const inventoryArray = [];
    const fixedOrder = this.getFixedIngredientOrder();
    
    // Header row
    inventoryArray.push([
      'STT',
      'Tn nguyn liu', 
      'n v',
      'S lng hin c',
      'S lng kim k',
      'Chnh lch',
      'Ghi ch'
    ]);

    // Data rows theo th t c nh
    fixedOrder.forEach((ingredientName, index) => {
      // Tm ingredient trong danh sch ingredients
      const ingredient = ingredients.find(ing => 
        ing.name.toUpperCase() === ingredientName.toUpperCase() ||
        ing.name.toUpperCase().includes(ingredientName.toUpperCase()) ||
        ingredientName.toUpperCase().includes(ing.name.toUpperCase())
      );
      
      if (ingredient) {
        // Tm trong inventoryData
        const ingredientId = Object.keys(inventoryData).find(id => {
          const invIngredient = ingredients.find(ing => 
            ing.id === id || 
            ing.id === parseInt(id).toString() ||
            ing.ingredientId === parseInt(id)
          );
          return invIngredient && invIngredient.id === ingredient.id;
        });
        
        const currentQuantity = ingredientId ? (inventoryData[ingredientId] || 0) : 0;
        const countedQuantity = 0; // S c nhp khi kim k
        const difference = countedQuantity - currentQuantity;
        
        inventoryArray.push([
          index + 1,
          ingredientName, // S dng tn c nh
          ingredient.unit,
          currentQuantity,
          countedQuantity,
          difference,
          ''
        ]);
      } else {
        // Nu khng tm thy ingredient, vn thm vo vi d liu mc nh
        inventoryArray.push([
          index + 1,
          ingredientName,
          'g', // n v mc nh
          0,
          0,
          0,
          'Cha c trong h thng'
        ]);
      }
    });

    return inventoryArray;
  }

  // To d liu bo co tng hp
  generateSummaryData(menuItems, recipes, ingredients, inventoryData) {
    const summaryArray = [];
    
    // Header row
    summaryArray.push([
      'STT',
      'Tn mn',
      'Nguyn liu',
      'n v',
      'S lng cn',
      'S lng c',
      'C th lm',
      'Ghi ch'
    ]);

    let rowIndex = 1;
    
    menuItems.forEach(menuItem => {
      const recipe = recipes[menuItem.id];
      if (recipe && Object.keys(recipe).length > 0) {
        Object.entries(recipe).forEach(([ingredientId, amount]) => {
          const ingredient = ingredients.find(ing => 
            ing.id === ingredientId || 
            ing.id === parseInt(ingredientId).toString() ||
            ing.ingredientId === parseInt(ingredientId)
          );
          
          if (ingredient) {
            const requiredAmount = typeof amount === 'object' ? amount.amount : amount;
            const availableAmount = inventoryData[ingredientId] || 0;
            const canMake = Math.floor(availableAmount / requiredAmount);
            
            summaryArray.push([
              rowIndex++,
              menuItem.name,
              ingredient.name,
              ingredient.unit,
              requiredAmount,
              availableAmount,
              canMake,
              canMake > 0 ? 'C th lm' : 'Thiu nguyn liu'
            ]);
          }
        });
      }
    });

    return summaryArray;
  }

  // Xut file Excel vi template
  async exportToExcel(inventoryData, ingredients, menuItems, recipes) {
    try {
      // To workbook mi
      const workbook = XLSX.utils.book_new();

      // Sheet 1: Kim k nguyn liu
      const inventorySheet = this.generateInventoryData(inventoryData, ingredients);
      const ws1 = XLSX.utils.aoa_to_sheet(inventorySheet);
      
      // nh dng header
      const headerStyle = {
        font: { bold: true, color: { rgb: "FFFFFF" } },
        fill: { fgColor: { rgb: "4472C4" } },
        alignment: { horizontal: "center", vertical: "center" }
      };
      
      // p dng style cho header row
      const range = XLSX.utils.decode_range(ws1['!ref']);
      for (let col = range.s.c; col <= range.e.c; col++) {
        const cellAddress = XLSX.utils.encode_cell({ r: 0, c: col });
        if (!ws1[cellAddress]) ws1[cellAddress] = {};
        ws1[cellAddress].s = headerStyle;
      }

      // t  rng ct
      ws1['!cols'] = [
        { wch: 5 },   // STT
        { wch: 25 },  // Tn nguyn liu
        { wch: 10 },  // n v
        { wch: 15 },  // S lng hin c
        { wch: 15 },  // S lng kim k
        { wch: 12 },  // Chnh lch
        { wch: 20 }   // Ghi ch
      ];

      XLSX.utils.book_append_sheet(workbook, ws1, 'Kim k nguyn liu');

      // Sheet 2: Bo co tng hp
      const summarySheet = this.generateSummaryData(menuItems, recipes, ingredients, inventoryData);
      const ws2 = XLSX.utils.aoa_to_sheet(summarySheet);
      
      // p dng style cho header row ca sheet 2
      const range2 = XLSX.utils.decode_range(ws2['!ref']);
      for (let col = range2.s.c; col <= range2.e.c; col++) {
        const cellAddress = XLSX.utils.encode_cell({ r: 0, c: col });
        if (!ws2[cellAddress]) ws2[cellAddress] = {};
        ws2[cellAddress].s = headerStyle;
      }

      // t  rng ct cho sheet 2
      ws2['!cols'] = [
        { wch: 5 },   // STT
        { wch: 20 },  // Tn mn
        { wch: 20 },  // Nguyn liu
        { wch: 10 },  // n v
        { wch: 12 },  // S lng cn
        { wch: 12 },  // S lng c
        { wch: 12 },  // C th lm
        { wch: 20 }   // Ghi ch
      ];

      XLSX.utils.book_append_sheet(workbook, ws2, 'Bo co tng hp');

      // Xut file
      const fileName = `Kiem_ke_nguyen_lieu_${new Date().toISOString().split('T')[0]}.xlsx`;
      XLSX.writeFile(workbook, fileName);

      return {
        success: true,
        fileName: fileName,
        message: 'Xut file Excel thnh cng!'
      };

    } catch (error) {
      console.error('Li khi xut Excel:', error);
      return {
        success: false,
        error: error.message,
        message: 'C li xy ra khi xut file Excel!'
      };
    }
  }

  // Xut file Excel vi layout 2 ct - Tn nguyn liu v Bn c
  exportSimpleInventory(inventoryData, ingredients) {
    try {
      const workbook = XLSX.utils.book_new();
      const fixedOrder = this.getFixedIngredientOrder();
      
      // To d liu vi layout 2 ct
      const data = [];
      
      // Header row
      data.push(['Tn nguyn liu', 'Bn c']);

      // Data rows - mi nguyn liu mt hng
      fixedOrder.forEach((ingredientName) => {
        // Tm ingredient trong danh sch ingredients
        const ingredient = ingredients.find(ing => 
          ing.name.toUpperCase() === ingredientName.toUpperCase() ||
          ing.name.toUpperCase().includes(ingredientName.toUpperCase()) ||
          ingredientName.toUpperCase().includes(ing.name.toUpperCase())
        );
        
        if (ingredient) {
          // Tm trong inventoryData
          const ingredientId = Object.keys(inventoryData).find(id => {
            const invIngredient = ingredients.find(ing => 
              ing.id === id || 
              ing.id === parseInt(id).toString() ||
              ing.ingredientId === parseInt(id)
            );
            return invIngredient && invIngredient.id === ingredient.id;
          });
          
          const quantity = ingredientId ? (inventoryData[ingredientId] || 0) : 0;
          data.push([ingredientName, quantity]);
        } else {
          // Nu khng tm thy ingredient, vn thm vo vi s lng 0
          data.push([ingredientName, 0]);
        }
      });

      // To worksheet
      const ws = XLSX.utils.aoa_to_sheet(data);
      
      // nh dng header
      const headerStyle = {
        font: { 
          bold: true, 
          color: { rgb: "FFFFFF" },
          size: 14
        },
        fill: { fgColor: { rgb: "2E5984" } },
        alignment: { 
          horizontal: "center", 
          vertical: "center" 
        },
        border: {
          top: { style: "thick", color: { rgb: "000000" } },
          bottom: { style: "thick", color: { rgb: "000000" } },
          left: { style: "thick", color: { rgb: "000000" } },
          right: { style: "thick", color: { rgb: "000000" } }
        }
      };
      
      // p dng style cho header row
      const range = XLSX.utils.decode_range(ws['!ref']);
      for (let col = range.s.c; col <= range.e.c; col++) {
        const cellAddress = XLSX.utils.encode_cell({ r: 0, c: col });
        if (!ws[cellAddress]) ws[cellAddress] = {};
        ws[cellAddress].s = headerStyle;
      }

      // Thm style cho data rows  tch bit vi header
      const dataStyle = {
        font: { size: 12 },
        border: {
          top: { style: "thin", color: { rgb: "CCCCCC" } },
          bottom: { style: "thin", color: { rgb: "CCCCCC" } },
          left: { style: "thin", color: { rgb: "CCCCCC" } },
          right: { style: "thin", color: { rgb: "CCCCCC" } }
        }
      };

      // p dng style cho data rows
      for (let row = 1; row <= range.e.r; row++) {
        for (let col = range.s.c; col <= range.e.c; col++) {
          const cellAddress = XLSX.utils.encode_cell({ r: row, c: col });
          if (!ws[cellAddress]) ws[cellAddress] = {};
          ws[cellAddress].s = dataStyle;
        }
      }

      // t  rng ct
      ws['!cols'] = [
        { wch: 30 },  // Ct "Tn nguyn liu"
        { wch: 15 }   // Ct "Bn c"
      ];

      XLSX.utils.book_append_sheet(workbook, ws, 'Bn c');

      // Xut file
      const fileName = `Ban_duoc_nguyen_lieu_${new Date().toISOString().split('T')[0]}.xlsx`;
      XLSX.writeFile(workbook, fileName);

      return {
        success: true,
        fileName: fileName,
        message: 'Xut file Excel theo layout 2 ct thnh cng!'
      };

    } catch (error) {
      console.error('Li khi xut Excel n gin:', error);
      return {
        success: false,
        error: error.message,
        message: 'C li xy ra khi xut file Excel!'
      };
    }
  }

  // Xut file Excel vi template t URL
  async exportWithTemplate(templateUrl, inventoryData, ingredients, menuItems, recipes) {
    try {
      // Ti template t URL
      const response = await fetch(templateUrl);
      if (!response.ok) {
        throw new Error('Khng th ti template t URL');
      }
      
      const arrayBuffer = await response.arrayBuffer();
      const workbook = XLSX.read(arrayBuffer, { type: 'array' });

      // Ly sheet u tin
      const firstSheetName = workbook.SheetNames[0];
      const worksheet = workbook.Sheets[firstSheetName];

      // Tm v tr bt u d liu (b qua header)
      const range = XLSX.utils.decode_range(worksheet['!ref']);
      let dataStartRow = 1; // Bt u t row 2 (index 1)

      // Tm dng trng u tin  chn d liu
      for (let row = 1; row <= range.e.r; row++) {
        const cellA = XLSX.utils.encode_cell({ r: row, c: 0 });
        if (!worksheet[cellA] || !worksheet[cellA].v) {
          dataStartRow = row;
          break;
        }
      }

      // Chn d liu kim k theo th t c nh
      const inventoryDataArray = this.generateInventoryData(inventoryData, ingredients);
      
      // B qua header row, ch ly data
      const dataRows = inventoryDataArray.slice(1);
      
      dataRows.forEach((row, index) => {
        row.forEach((cellValue, colIndex) => {
          const cellAddress = XLSX.utils.encode_cell({ 
            r: dataStartRow + index, 
            c: colIndex 
          });
          worksheet[cellAddress] = { v: cellValue };
        });
      });

      // Cp nht range
      const newRange = XLSX.utils.encode_range({
        s: { r: 0, c: 0 },
        e: { r: dataStartRow + dataRows.length - 1, c: 6 }
      });
      worksheet['!ref'] = newRange;

      // Xut file
      const fileName = `Kiem_ke_nguyen_lieu_template_${new Date().toISOString().split('T')[0]}.xlsx`;
      XLSX.writeFile(workbook, fileName);

      return {
        success: true,
        fileName: fileName,
        message: 'Xut file Excel vi template thnh cng!'
      };

    } catch (error) {
      console.error('Li khi xut Excel vi template:', error);
      return {
        success: false,
        error: error.message,
        message: 'C li xy ra khi xut file Excel vi template!'
      };
    }
  }
}

// To instance duy nht
const excelService = new ExcelService();

export default excelService;
