import bcrypt from 'bcryptjs';

class AuthService {
  constructor() {
    this.apiUrl = 'https://683dc621199a0039e9e6d42d.mockapi.io/accounts';
    this.currentUser = null;
  }

  // ng nhp
  async login(username, password) {
    try {
      const response = await fetch(this.apiUrl);
      if (!response.ok) {
        throw new Error('Khng th kt ni n server');
      }
      
      const accounts = await response.json();
      
      // Tm ti khon theo username
      const account = accounts.find(acc => 
        acc.username === username && 
        acc.status === true && 
        acc.isApprove === true
      );
      
      if (!account) {
        throw new Error('Ti khon khng tn ti hoc cha c ph duyt');
      }
      
      // So snh mt khu vi bcrypt (6 rounds)
      const isPasswordValid = await bcrypt.compare(password, account.password);
      if (!isPasswordValid) {
        throw new Error('Mt khu khng ng');
      }
      
      // Lu thng tin user vo localStorage
      this.currentUser = {
        id: account.id,
        username: account.username,
        fullname: account.fullname,
        role: account.role,
        avatar: account.avatar
      };
      
      localStorage.setItem('currentUser', JSON.stringify(this.currentUser));
      
      return {
        success: true,
        user: this.currentUser,
        message: 'ng nhp thnh cng!'
      };
      
    } catch (error) {
      return {
        success: false,
        error: error.message,
        message: 'ng nhp tht bi!'
      };
    }
  }

  // ng xut
  logout() {
    this.currentUser = null;
    localStorage.removeItem('currentUser');
  }

  // Ly thng tin user hin ti
  getCurrentUser() {
    if (this.currentUser) {
      return this.currentUser;
    }
    
    const stored = localStorage.getItem('currentUser');
    if (stored) {
      this.currentUser = JSON.parse(stored);
      return this.currentUser;
    }
    
    return null;
  }

  // Kim tra  ng nhp cha
  isAuthenticated() {
    return this.getCurrentUser() !== null;
  }

  // Kim tra quyn admin
  isAdmin() {
    const user = this.getCurrentUser();
    return user && user.role === 'ADMIN';
  }

  // Hash mt khu vi 6 rounds ( s dng trong tng lai)
  async hashPassword(password) {
    const saltRounds = 6;
    return await bcrypt.hash(password, saltRounds);
  }
}

// To instance duy nht
const authService = new AuthService();

export default authService;
