import mockAPIService from './mockAPIService';
import toastService from './toastService';

// Service  qun l d liu - c/ghi t file JSON v MockAPI
class DataService {
  constructor() {
    // D liu s c load t file JSON v MockAPI
    this.menuItems = [];
    this.ingredients = [];
    this.recipes = {};
    this.sales = {};
    this.useMockAPI = true; // Flag  bt/tt MockAPI
    this.isInitialized = false; // Flag  kim tra  khi to cha
  }

  // Ly danh sch mn
  getMenuItems() {
    return this.menuItems;
  }

  // Ly danh sch nguyn liu
  getIngredients() {
    return this.ingredients;
  }

  // Ly cng thc cho mn
  getRecipe(itemId) {
    return this.recipes[itemId] || {};
  }

  // Ly tt c cng thc
  getAllRecipes() {
    return this.recipes;
  }

  // Cp nht cng thc
  async updateRecipe(itemId, recipe) {
    this.recipes[itemId] = recipe;
    
    
    if (this.useMockAPI) {
      try {
        const menuItem = this.menuItems.find(item => item.id === itemId);
        const mockAPIRecipe = mockAPIService.convertToMockAPIFormat(itemId, recipe, menuItem, this.ingredients);
        
        // Debug: Kim tra tt c recipes trc
        try {
          await mockAPIService.debugGetAllRecipes();
        } catch (debugError) {
        }
        
        // Debug: Kim tra recipe c th
        try {
          await mockAPIService.debugGetRecipeById(itemId);
        } catch (debugError) {
        }
        
        // Kim tra xem recipe  tn ti trong MockAPI cha
        const recipeExists = await mockAPIService.checkRecipeExists(itemId);
        
        if (recipeExists) {
          // Recipe  tn ti, cp nht
          await mockAPIService.updateRecipe(itemId, mockAPIRecipe);
        } else {
          // Recipe cha tn ti, to mi
          await mockAPIService.createRecipe(mockAPIRecipe);
        }
      } catch (error) {
        toastService.error('Li khi cp nht cng thc trong MockAPI');
        throw error;
      }
    } else {
      await this.saveRecipesToAPI();
    }
  }

  // Thm mn mi
  async addMenuItem(item) {
    const newId = Math.max(...this.menuItems.map(i => i.id), 0) + 1;
    const newItem = { ...item, id: newId };
    this.menuItems.push(newItem);
    
    if (this.useMockAPI) {
      // Ch lu vo MockAPI, khng fallback local
    } else {
      await this.saveMenuItemsToAPI();
    }
    return newItem;
  }

  // Cp nht mn
  async updateMenuItem(itemId, updates) {
    const index = this.menuItems.findIndex(item => item.id === itemId);
    if (index !== -1) {
      this.menuItems[index] = { ...this.menuItems[index], ...updates };
      
      if (this.useMockAPI) {
        // Ch lu vo MockAPI, khng fallback local
      } else {
        await this.saveMenuItemsToAPI();
      }
    }
  }

  // Xa mn
  async deleteMenuItem(itemId) {
    this.menuItems = this.menuItems.filter(item => item.id !== itemId);
    delete this.recipes[itemId];
    
    if (this.useMockAPI) {
      // Xa recipe khi MockAPI nu c
      try {
        await mockAPIService.deleteRecipe(itemId);
      } catch (error) {
        toastService.error('Li khi xa cng thc khi MockAPI');
        throw error;
      }
    } else {
      await this.saveMenuItemsToAPI();
      await this.saveRecipesToAPI();
    }
  }

  // Thm nguyn liu mi
  async addIngredient(ingredient) {
    if (this.useMockAPI) {
      try {
        const newIngredient = await mockAPIService.createIngredient(ingredient);
        this.ingredients.push(newIngredient);
        return newIngredient;
      } catch (error) {
        toastService.error('Li khi to nguyn liu trong MockAPI');
        throw error;
      }
    } else {
      const newId = Math.max(...this.ingredients.map(i => i.id), 0) + 1;
      const newIngredient = { ...ingredient, id: newId };
      this.ingredients.push(newIngredient);
      await this.saveIngredientsToAPI();
      return newIngredient;
    }
  }

  // Cp nht nguyn liu
  async updateIngredient(ingredientId, updates) {
    const index = this.ingredients.findIndex(ing => ing.id === ingredientId);
    if (index !== -1) {
      this.ingredients[index] = { ...this.ingredients[index], ...updates };
      
      if (this.useMockAPI) {
        try {
          const ingredientExists = await mockAPIService.checkIngredientExists(ingredientId);
          
          if (ingredientExists) {
            // Ingredient  tn ti, cp nht
            await mockAPIService.updateIngredient(ingredientId, this.ingredients[index]);
          } else {
            // Ingredient cha tn ti, to mi
            await mockAPIService.createIngredient(this.ingredients[index]);
          }
        } catch (error) {
          toastService.error('Li khi cp nht nguyn liu trong MockAPI');
          throw error;
        }
      } else {
        await this.saveIngredientsToAPI();
      }
    }
  }

  // Xa nguyn liu
  async deleteIngredient(ingredientId) {
    
    // Tm ingredient trc khi xa  debug
    const ingredientToDelete = this.ingredients.find(ing => ing.id === ingredientId);
    if (ingredientToDelete) {
    } else {
    }
    
    // Xa nguyn liu khi local data
    this.ingredients = this.ingredients.filter(ing => ing.id !== ingredientId);
    
    // Xa nguyn liu khi tt c cng thc
    const updatedRecipes = {};
    Object.keys(this.recipes).forEach(itemId => {
      const recipe = { ...this.recipes[itemId] };
      delete recipe[ingredientId];
      updatedRecipes[itemId] = recipe;
    });
    this.recipes = updatedRecipes;
    
    if (this.useMockAPI) {
      try {
        // Xa ingredient trong MockAPI
        await mockAPIService.deleteIngredient(ingredientId);
        
        // Cp nht tt c recipes trong MockAPI
        const updatePromises = Object.keys(this.recipes).map(itemId => 
          this.updateRecipe(itemId, this.recipes[itemId])
        );
        await Promise.all(updatePromises);
        
      } catch (error) {
        toastService.error('Li khi xa nguyn liu trong MockAPI');
        throw error;
      }
    } else {
      await this.saveIngredientsToAPI();
      await this.saveRecipesToAPI();
    }
  }

  // Thm recipe mi (tng thch vi MockAPI)
  async addRecipe(itemId, recipe) {
    this.recipes[itemId] = recipe;
    
    if (this.useMockAPI) {
      try {
        const menuItem = this.menuItems.find(item => item.id === itemId);
        const mockAPIRecipe = mockAPIService.convertToMockAPIFormat(itemId, recipe, menuItem, this.ingredients);
        await mockAPIService.createRecipe(mockAPIRecipe);
      } catch (error) {
        toastService.error('Li khi to cng thc trong MockAPI');
        throw error;
      }
    } else {
      await this.saveRecipesToAPI();
    }
  }

  // Xa recipe (tng thch vi MockAPI)
  async deleteRecipe(itemId) {
    delete this.recipes[itemId];
    
    if (this.useMockAPI) {
      try {
        await mockAPIService.deleteRecipe(itemId);
      } catch (error) {
        toastService.error('Li khi xa cng thc trong MockAPI');
        throw error;
      }
    } else {
      await this.saveRecipesToAPI();
    }
  }

  // Toggle MockAPI mode
  toggleMockAPIMode() {
    this.useMockAPI = !this.useMockAPI;
    return this.useMockAPI;
  }

  // Ly trng thi MockAPI
  isMockAPIEnabled() {
    return this.useMockAPI;
  }

  // Kim tra trng thi MockAPI
  checkMockAPIStatus() {
    const status = {
      mockAPI: {
        enabled: this.useMockAPI,
        ingredients: this.ingredients.length,
        recipes: Object.keys(this.recipes).length,
        menuItems: this.menuItems.length
      }
    };
    
    return status;
  }

  // Ly d liu menu items mc nh
  getDefaultMenuItems() {
    return [
      { "id": 1, "name": "C ph en" },
      { "id": 2, "name": "C ph sa" },
      { "id": 3, "name": "Sinh t b" },
      { "id": 4, "name": "Cacao sa" },
      { "id": 5, "name": "Bc xiu" },
      { "id": 6, "name": "Americano" },
      { "id": 7, "name": "Sa ti cafe" },
      { "id": 8, "name": "Cafe mui" },
      { "id": 9, "name": "Cacao mui" },
      { "id": 10, "name": "Cacao  xay" },
      { "id": 11, "name": "C ph  xay" },
      { "id": 12, "name": "p to" },
      { "id": 13, "name": "p cam" },
      { "id": 14, "name": "p thm" },
      { "id": 15, "name": "Da ti" },
      { "id": 16, "name": "Sinh t da" },
      { "id": 17, "name": "Matcha sa yn mch" },
      { "id": 18, "name": "Sa chua " },
      { "id": 19, "name": "Sa chua vit qut" },
      { "id": 20, "name": "Tr gng" },
      { "id": 21, "name": "Nc su" }
    ];
  }

  // Sync d liu t local ln MockAPI
  async syncToMockAPI() {
    if (!this.useMockAPI) {
      return;
    }

    try {
      
      // Sync ingredients
      for (const ingredient of this.ingredients) {
        try {
          await mockAPIService.createIngredient(ingredient);
        } catch (error) {
          if (error.message.includes('409')) {
            // Ingredient  tn ti, b qua
          } else {
          }
        }
      }
      
      // Sync recipes
      for (const [itemId, recipe] of Object.entries(this.recipes)) {
        try {
          await this.updateRecipe(itemId, recipe);
        } catch (error) {
        }
      }
      
    } catch (error) {
    }
  }

  // Ly d liu bn hng theo ngy
  getSalesByDate(date) {
    return this.sales[date] || {};
  }

  // Cp nht d liu bn hng
  updateSales(date, salesData) {
    this.sales[date] = salesData;
    this.saveSalesToLocalStorage();
  }

  // c d liu t MockAPI
  async loadDataFromAPI() {
    if (!this.useMockAPI) {
      throw new Error('MockAPI khng c bt');
    }

    try {
      
      // Load ingredients t MockAPI
      this.ingredients = await mockAPIService.getAllIngredients();

      // Load recipes t MockAPI
      const mockAPIRecipes = await mockAPIService.getAllRecipes();
      this.recipes = mockAPIService.convertFromMockAPIFormat(mockAPIRecipes);

      // Load menu items t MockAPI (to t recipes)
      this.menuItems = this.getDefaultMenuItems();

      // Load sales t localStorage
      this.loadSalesFromLocalStorage();
      
    } catch (error) {
      toastService.error('Khng th kt ni n MockAPI. Vui lng kim tra kt ni mng.');
      throw error;
    }
  }





  // Lu sales vo localStorage
  saveSalesToLocalStorage() {
    try {
      localStorage.setItem('ingredientsTool_sales', JSON.stringify(this.sales));
    } catch (error) {
    }
  }

  // Load sales t localStorage
  loadSalesFromLocalStorage() {
    try {
      const savedSales = localStorage.getItem('ingredientsTool_sales');
      if (savedSales) {
        this.sales = JSON.parse(savedSales);
      }
    } catch (error) {
    }
  }



  // Khi to service
  async init() {
    if (this.isInitialized) {
      return; //  khi to ri
    }
    
    if (this.useMockAPI) {
      // Ch load t MockAPI
      try {
        await this.loadDataFromAPI();
      } catch (error) {
        toastService.error('Khng th kt ni n MockAPI. Vui lng kim tra kt ni mng.');
        throw error;
      }
    } else {
      // MockAPI b tt - khng h tr
      toastService.error('MockAPI phi c bt  s dng ng dng');
      throw new Error('MockAPI phi c bt');
    }
    
    this.isInitialized = true;
  }
}

// To instance duy nht
const dataService = new DataService();

export default dataService;
