import React, { useState, useEffect } from 'react';
import dataService from '../services/dataService';
import toastService from '../services/toastService';
import Modal from './Modal';
import Spinner from './Spinner';

const IngredientsPage = () => {
  // Ly d liu t service
  const [ingredients, setIngredients] = useState([]);
  const [isModalOpen, setIsModalOpen] = useState(false);
  const [editingIngredient, setEditingIngredient] = useState(null);
  const [formData, setFormData] = useState({ name: '', unit: '' });
  const [isDataLoading, setIsDataLoading] = useState(true);

  // Load d liu khi component mount
  useEffect(() => {
    const loadIngredients = async () => {
      setIsDataLoading(true);
      try {
        // m bo dataService  c khi to
        if (!dataService.isInitialized) {
          await dataService.init();
        }
        setIngredients(dataService.getIngredients());
      } catch (error) {
        setIngredients([]);
      } finally {
        setIsDataLoading(false);
      }
    };
    
    loadIngredients();
  }, []);

  // Hm m modal thm mi
  const openAddModal = () => {
    setEditingIngredient(null);
    setFormData({ name: '', unit: '' });
    setIsModalOpen(true);
  };

  // Hm m modal chnh sa
  const openEditModal = (ingredient) => {
    setEditingIngredient(ingredient);
    setFormData({ name: ingredient.name, unit: ingredient.unit });
    setIsModalOpen(true);
  };

  // Hm ng modal
  const closeModal = () => {
    setIsModalOpen(false);
    setEditingIngredient(null);
    setFormData({ name: '', unit: '' });
  };

  // Hm x l thay i form
  const handleInputChange = (e) => {
    const { name, value } = e.target;
    setFormData(prev => ({
      ...prev,
      [name]: value
    }));
  };

  // Hm lu nguyn liu
  const handleSave = () => {
    if (!formData.name.trim() || !formData.unit.trim()) {
      toastService.warning('Vui lng in y  thng tin!');
      return;
    }

    if (editingIngredient) {
      // Cp nht nguyn liu
      dataService.updateIngredient(editingIngredient.id, formData);
      setIngredients(dataService.getIngredients());
      toastService.success(' cp nht nguyn liu thnh cng!');
    } else {
      // Thm nguyn liu mi
      dataService.addIngredient(formData);
      setIngredients(dataService.getIngredients());
      toastService.success(' thm nguyn liu mi thnh cng!');
    }
    
    closeModal();
  };

  // Hm xa nguyn liu
  const handleDelete = async (ingredientId) => {
    if (window.confirm('Bn c chc chn mun xa nguyn liu ny? Hnh ng ny s xa nguyn liu khi tt c cng thc.')) {
      try {
        await dataService.deleteIngredient(ingredientId);
        setIngredients(dataService.getIngredients());
        toastService.success(' xa nguyn liu thnh cng!');
      } catch (error) {
        toastService.error('C li xy ra khi xa nguyn liu!');
      }
    }
  };

  // Show spinner while loading data
  if (isDataLoading) {
    return (
      <div className="container" style={{ paddingTop: '2rem', paddingBottom: '2rem' }}>
        <Spinner text="ang ti danh sch nguyn liu..." />
      </div>
    );
  }

  return (
    <div className="container" style={{ paddingTop: '2rem', paddingBottom: '2rem' }}>
      {/* Header */}
      <div className="card text-center fade-in" style={{ 
        background: 'linear-gradient(135deg, #667eea 0%, #764ba2 100%)',
        color: 'white',
        border: 'none',
        marginBottom: '2rem'
      }}>
        <h1 className="text-xl" style={{ 
          marginBottom: '0.5rem',
          fontSize: '2.5rem',
          fontWeight: 'bold'
        }}>
           Qun L Nguyn Liu
        </h1>
        <p className="text-lg" style={{ opacity: 0.9 }}>
          Thm, sa, xa nguyn liu v n v o
        </p>
      </div>

      {/* Thng k */}
      <div className="card fade-in" style={{ marginBottom: '2rem' }}>
        <div className="grid grid-4 text-center">
          <div>
            <div className="text-lg font-bold text-primary">{ingredients.length}</div>
            <div className="text-sm text-muted">Tng nguyn liu</div>
          </div>
          <div>
            <div className="text-lg font-bold text-success">
              {[...new Set(ingredients.map(ing => ing.unit))].length}
            </div>
            <div className="text-sm text-muted">n v o</div>
          </div>
          <div>
            <div className="text-lg font-bold text-warning">
              {ingredients.filter(ing => ing.unit === 'g').length}
            </div>
            <div className="text-sm text-muted">Theo gram</div>
          </div>
          <div>
            <div className="text-lg font-bold text-danger">
              {ingredients.filter(ing => ing.unit === 'ml').length}
            </div>
            <div className="text-sm text-muted">Theo ml</div>
          </div>
        </div>
      </div>

      {/* Nt thm mi */}
      <div className="card fade-in" style={{ marginBottom: '2rem' }}>
        <div className="flex-between">
          <h2 className="text-lg font-bold"> Danh Sch Nguyn Liu</h2>
          <button
            onClick={openAddModal}
            className="btn btn-success"
          >
             Thm Nguyn Liu
          </button>
        </div>
      </div>

      {/* Danh sch nguyn liu */}
      <div className="card fade-in">
        {ingredients.length > 0 ? (
          <div className="grid grid-3" style={{ gap: '1rem' }}>
            {ingredients.map(ingredient => (
              <div key={ingredient.id} className="card" style={{
                padding: '1rem',
                backgroundColor: '#f8f9fa',
                border: '2px solid #e9ecef',
                transition: 'all 0.3s ease'
              }}>
                <div className="text-center mb-2">
                  <div className="font-bold text-lg" style={{ color: '#333' }}>
                    {ingredient.name}
                  </div>
                  <div className="badge badge-primary">
                    {ingredient.unit}
                  </div>
                </div>
                
                <div className="flex gap-1 justify-center">
                  <button
                    onClick={() => openEditModal(ingredient)}
                    className="btn btn-warning btn-sm"
                    style={{ flex: 1 }}
                  >
                     Sa
                  </button>
                  <button
                    onClick={() => handleDelete(ingredient.id)}
                    className="btn btn-danger btn-sm"
                    style={{ flex: 1 }}
                  >
                     Xa
                  </button>
                </div>
              </div>
            ))}
          </div>
        ) : (
          <div className="text-center" style={{ padding: '2rem' }}>
            <div style={{ fontSize: '3rem', marginBottom: '1rem' }}></div>
            <p className="text-muted text-lg">
              Cha c nguyn liu no
            </p>
            <button
              onClick={openAddModal}
              className="btn btn-primary mt-2"
            >
               Thm Nguyn Liu u Tin
            </button>
          </div>
        )}
      </div>

      {/* Modal thm/sa nguyn liu */}
      <Modal
        isOpen={isModalOpen}
        onClose={closeModal}
        title={editingIngredient ? ` Sa Nguyn Liu` : ` Thm Nguyn Liu Mi`}
        size="small"
      >
        <div className="fade-in">
          <div className="form-group mb-3">
            <label className="form-label">Tn nguyn liu</label>
            <input
              type="text"
              name="name"
              value={formData.name}
              onChange={handleInputChange}
              className="form-control"
              placeholder="V d: C ph, Sa ti, ng..."
            />
          </div>

          <div className="form-group mb-3">
            <label className="form-label">n v o</label>
            <select
              name="unit"
              value={formData.unit}
              onChange={handleInputChange}
              className="form-control"
            >
              <option value="">Chn n v o</option>
              <option value="g">Gram (g)</option>
              <option value="ml">Milliliter (ml)</option>
              <option value="qu">Qu</option>
              <option value="ly">Ly</option>
              <option value="mung">Mung</option>
              <option value="gi">Gi</option>
              <option value="kg">Kilogram (kg)</option>
              <option value="l">Liter (l)</option>
              <option value="ci">Ci</option>
              <option value="ming">Ming</option>
            </select>
          </div>

          <div className="flex gap-2 justify-center" style={{ 
            paddingTop: '0.5rem',
            borderTop: '1px solid #e5e7eb'
          }}>
            <button
              onClick={handleSave}
              className="btn btn-success btn-sm"
              style={{ minWidth: '80px' }}
            >
               Lu
            </button>
            <button
              onClick={closeModal}
              className="btn btn-secondary btn-sm"
              style={{ minWidth: '80px' }}
            >
               Hy
            </button>
          </div>
        </div>
      </Modal>
    </div>
  );
};

export default IngredientsPage;
